<template>
    <div> 
      <div class="popu">
        <h1><strong>Els Jocs més Populars ara mateix</strong></h1>
      </div>
        <div class="container-md">
            <b-card-group columns>
              <div columns v-for="proyecto in jocs" :key="proyecto.id">
               <CardFotos :srcimg="proyecto.srcimg" :alt="proyecto.alt" :titulo="proyecto.titulo"></CardFotos>
              </div>
             </b-card-group>
         </div>
   </div>
  
</template>
  
<script>
import CardFotos from '@/components/CardFotos.vue'
const lstJocsJson = '../Jocs.json'

export default{
    props:{
        name:{
            type: String,
            default: 'LlistaJocs'
        }
    },
    components: {
    CardFotos
},
    data() {
        return{
            jocs: []
        }
    },
    mounted() {
        this.fetchData()
    },
    methods: {
        async fetchData() {
            const res = await fetch (lstJocsJson);
            const val= await res.json();
            this.jocs=val;
            console.log(val);
        }
    }

}
  </script>

<style scoped>
.container-md{
  text-align: center;
  color: black;
}
.popu{
  text-align: center;
  font-size: 4vw;
  padding: 1vw;
  margin: auto;
  margin-top: 1vw;
  margin-bottom: 1vw;
}
.container-md b-card-group{
  background-color: rgb(194, 124, 90);
}
</style>